/* src/App.css - MODERN, CLEAN VERSION */

/* Import modern font */
@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700;800&display=swap');

/* --- CSS Variables --- */
:root {
  /* Dark theme colors */
  --primary-bg: #000000;
  --secondary-bg: #111111;
  --accent-color: #5eead4;
  --text-color: #ffffff;
  --subtext-color: #aaaaaa;
  --gradient-start: #5eead4;
  --gradient-end: #2dd4bf;
  --card-bg: rgba(17, 17, 17, 0.7);
  
  /* Light theme colors */
  --light-primary-bg: #ffffff;
  --light-secondary-bg: #f8fafc;
  --light-accent-color: #0f766e;
  --light-text-color: #1e293b;
  --light-subtext-color: #64748b;
  --light-card-bg: rgba(248, 250, 252, 0.8);
  
  /* Modern font stack */
  --font-family: 'Plus Jakarta Sans', 'Inter', system-ui, sans-serif;
  
  /* Enhanced shadows */
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 20px 25px rgba(0, 0, 0, 0.1);
  --shadow-accent: 0 8px 16px rgba(94, 234, 212, 0.2);
  
  /* Spacing system */
  --space-3xs: 0.25rem;
  --space-2xs: 0.5rem;
  --space-xs: 0.75rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  --space-2xl: 4rem;
  
  /* Border radius */
  --radius-sm: 0.25rem;
  --radius-md: 0.5rem;
  --radius-lg: 1rem;
  --radius-pill: 9999px;
  
  /* Animation */
  --transition-fast: 0.2s;
  --transition-base: 0.3s;
  --transition-slow: 0.5s;
  --ease-in-out: cubic-bezier(0.4, 0, 0.2, 1);
  --ease-out: cubic-bezier(0, 0, 0.2, 1);
  --ease-in: cubic-bezier(0.4, 0, 1, 1);
}

/* Light theme */
[data-theme="light"] {
  --primary-bg: var(--light-primary-bg);
  --secondary-bg: var(--light-secondary-bg);
  --accent-color: var(--light-accent-color);
  --text-color: var(--light-text-color);
  --subtext-color: var(--light-subtext-color);
  --card-bg: var(--light-card-bg);
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.05);
  --shadow-lg: 0 20px 25px rgba(0, 0, 0, 0.05);
  --shadow-accent: 0 8px 16px rgba(15, 118, 110, 0.1);
}

/* --- Modern CSS Reset --- */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* --- Core Layout --- */
body {
  background: var(--primary-bg) radial-gradient(ellipse at 50% -20%, rgba(94, 234, 212, 0.15), transparent 40%);
  color: var(--subtext-color);
  font-family: var(--font-family);
  line-height: 1.6;
  text-rendering: optimizeSpeed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background-color var(--transition-base) var(--ease-out);
  padding-top: 80px; /* Prevent header overlap */
}

.App {
  position: relative;
}

/* Global scrollbar styling */
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: var(--primary-bg);
}
::-webkit-scrollbar-thumb {
  background: #333333;
  border-radius: var(--radius-pill);
}
::-webkit-scrollbar-thumb:hover {
  background: var(--accent-color);
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--space-md);
  width: 100%;
}

/* --- Navigation --- */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-sm) 0;
  position: fixed; /* Fixed to top */
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  z-index: 1000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  transition: all var(--transition-base) var(--ease-out);
}

[data-theme="light"] nav {
  background: rgba(255, 255, 255, 0.85);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 0;
  padding-bottom: 0;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo {
  font-size: clamp(1.5rem, 1.5vw, 2rem);
  font-weight: 700;
  text-decoration: none;
  background: linear-gradient(90deg, var(--gradient-start), var(--gradient-end));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all var(--transition-base) var(--ease-out);
  position: relative;
  padding: var(--space-xs) 0;
}

/* Desktop Navigation */
.desktop-nav-links {
  display: flex;
  gap: var(--space-lg);
}

.desktop-nav-links a {
  color: var(--subtext-color);
  text-decoration: none;
  font-size: clamp(0.9rem, 1vw, 1rem);
  font-weight: 500;
  position: relative;
  transition: all var(--transition-base) var(--ease-out);
  padding: var(--space-xs) 0;
  cursor: pointer;
}

.desktop-nav-links a:hover,
.desktop-nav-links a.active {
  color: var(--text-color);
}

/* Mobile Navigation */
.mobile-nav-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: var(--primary-bg);
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: var(--space-xl);
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--transition-base) var(--ease-in-out);
}

.mobile-nav-overlay.open {
  opacity: 1;
  pointer-events: auto;
}

.mobile-nav-overlay a {
  color: var(--text-color);
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 700;
}

/* Hamburger Menu */
.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  z-index: 1002;
}

.menu-toggle .bar {
  width: 2rem;
  height: 3px;
  background-color: var(--text-color);
  border-radius: var(--radius-pill);
  transition: all var(--transition-base) var(--ease-in-out);
}

.menu-toggle.open .bar:nth-child(1) { transform: translateY(10px) rotate(45deg); }
.menu-toggle.open .bar:nth-child(2) { opacity: 0; }
.menu-toggle.open .bar:nth-child(3) { transform: translateY(-10px) rotate(-45deg); }

/* Theme toggle removed - styles deleted to simplify header */

/* --- Hero Section --- */
.hero {
  min-height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: var(--space-2xl) 0;
  position: relative;
  text-align: center;
}

/* Profile Pic - UPDATED SIZE */
.profile-pic {
  width: clamp(150px, 20vw, 220px);
  height: clamp(150px, 20vw, 220px);
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid var(--accent-color);
  box-shadow: 0 4px 20px rgba(94, 234, 212, 0.3);
  background: linear-gradient(135deg, var(--gradient-start), var(--gradient-end));
  padding: 5px;
  margin-bottom: var(--space-lg);
  transition: transform 0.3s var(--ease-out);
}

.profile-pic:hover {
  transform: scale(1.05);
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hero-cta {
  display: flex;
  gap: var(--space-md);
  margin: var(--space-lg) 0;
  flex-wrap: wrap;
  justify-content: center;
}

.cta-button.primary {
  background: linear-gradient(135deg, var(--gradient-start), var(--gradient-end));
  color: var(--primary-bg);
  border: none;
  display: flex;
  align-items: center;
  gap: var(--space-xs);
  font-weight: 600;
  padding: var(--space-sm) var(--space-lg);
  border-radius: var(--radius-md);
  cursor: pointer;
}

.cta-button.secondary {
  background: transparent;
  color: var(--accent-color);
  border: 2px solid var(--accent-color);
  display: flex;
  align-items: center;
  gap: var(--space-xs);
  font-weight: 600;
  padding: var(--space-sm) var(--space-lg);
  border-radius: var(--radius-md);
  cursor: pointer;
}

.cta-button.secondary:hover {
  background: var(--accent-color);
  color: var(--primary-bg);
}

.hero-social {
  display: flex;
  gap: var(--space-lg);
  margin-top: var(--space-lg);
  justify-content: center;
}

.hero-social a {
  color: var(--subtext-color);
  font-size: 1.5rem;
  transition: all var(--transition-base) var(--ease-out);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: var(--card-bg);
  border: 1px solid rgba(94, 234, 212, 0.1);
}

.hero-social a:hover {
  color: var(--accent-color);
  border-color: var(--accent-color);
  transform: translateY(-3px);
}

.small-heading {
  color: var(--accent-color);
  font-size: clamp(1rem, 1.2vw, 1.2rem);
  margin-bottom: var(--space-xs);
  letter-spacing: 0.1em;
  font-weight: 600;
}

.big-heading {
  color: var(--text-color);
  font-size: clamp(2.5rem, 5vw, 4.5rem);
  margin-bottom: var(--space-xs);
  line-height: 1.1;
  font-weight: 800;
}

.sub-heading {
  color: var(--subtext-color);
  font-size: clamp(1.2rem, 2vw, 2rem);
  margin-bottom: var(--space-md);
}

.hero-description {
  max-width: 650px;
  font-size: clamp(1rem, 1.1vw, 1.1rem);
  margin-bottom: var(--space-lg);
  line-height: 1.8;
  color: var(--subtext-color);
}

/* --- Section Styling --- */
section {
  padding: var(--space-xl) 0;
  position: relative;
}

.section-heading {
  font-size: clamp(1.5rem, 2vw, 2rem);
  color: var(--text-color);
  margin-bottom: var(--space-lg);
  position: relative;
  padding-bottom: var(--space-sm);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.section-heading::before {
  content: '';
  height: 2px;
  width: 60px;
  background: var(--accent-color);
  display: block;
}

.gradient-text {
  background: linear-gradient(90deg, var(--gradient-start), var(--gradient-end));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* --- Projects Grid --- */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: var(--space-lg);
  margin-top: var(--space-lg);
}

.project-card {
  background: var(--card-bg);
  padding: var(--space-lg);
  border-radius: var(--radius-lg);
  border: 1px solid rgba(94, 234, 212, 0.05);
  transition: transform 0.3s ease;
}

.project-title {
  color: var(--text-color);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.project-details {
    list-style: none;
    color: var(--subtext-color);
}

.project-details li {
    margin-bottom: 0.5rem;
}

.project-details a {
    color: var(--accent-color);
    text-decoration: none;
}

/* --- Skills --- */
.skill-category { margin-bottom: 2rem; }
.skill-category h3 { color: var(--text-color); margin-bottom: 1rem; }
.skills-container { display: flex; flex-wrap: wrap; gap: 10px; }
.skill-tag {
    background: var(--card-bg);
    color: var(--text-color);
    padding: 8px 16px;
    border-radius: 20px;
    border: 1px solid rgba(94, 234, 212, 0.2);
    display: flex;
    align-items: center;
    gap: 8px;
}

/* --- Education --- */
.education-timeline {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}
.education-item {
    background: var(--card-bg);
    padding: 1.5rem;
    border-radius: 1rem;
    border-left: 4px solid var(--accent-color);
}
.education-item h3 { color: var(--text-color); margin-bottom: 0.5rem; }
.education-item p { color: var(--subtext-color); }

/* --- Certifications --- */
.certification-badges {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
}
.badge {
    background: var(--card-bg);
    padding: 1rem;
    border-radius: 0.5rem;
    color: var(--text-color);
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 10px;
    border: 1px solid rgba(94, 234, 212, 0.2);
}

/* --- Contact --- */
.contact-info {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
}
.contact-card {
    background: var(--card-bg);
    padding: 2rem;
    border-radius: 1rem;
    flex: 1;
    min-width: 250px;
    text-align: center;
}
.contact-card h3 { color: var(--text-color); margin-bottom: 1rem; }
.contact-card p, .contact-card a { color: var(--subtext-color); text-decoration: none; }

/* --- Footer --- */
footer {
    text-align: center;
    padding: 2rem 0;
    margin-top: 4rem;
    border-top: 1px solid rgba(255,255,255,0.1);
}
.social-links {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 1rem;
}
.social-links a {
    color: var(--subtext-color);
    font-size: 1.5rem;
}

/* --- Responsive Design --- */
@media (min-width: 900px) {
  /* Desktop Layout for Hero: Side by Side */
  .hero {
    flex-direction: row-reverse; /* Image on right, Text on left */
    justify-content: space-between;
    text-align: left;
    padding: 0 var(--space-xl);
  }

  .hero-content {
    align-items: flex-start;
    max-width: 60%;
  }

  .hero-cta, .hero-social {
    justify-content: flex-start;
  }

  .profile-pic {
    width: 350px;
    height: 350px;
    margin-bottom: 0;
  }
}

@media (max-width: 900px) {
  .desktop-nav-links { display: none; }
  .menu-toggle { display: flex; }
}


