.big-heading { color: var(--text-color); font-size: clamp(2.5rem, 5vw, 4.5rem); font-weight: 800; line-height: 1.1; margin-bottom: 0.5rem; }
.sub-heading { color: var(--subtext-color); font-size: clamp(1.2rem, 2vw, 2rem); margin-bottom: 1.5rem; }
.hero-description { max-width: 650px; font-size: clamp(1rem, 1.1vw, 1.1rem); margin-bottom: 2rem; color: var(--subtext-color); }
.gradient-text { background: linear-gradient(90deg, var(--gradient-start), var(--gradient-end)); -webkit-background-clip: text; -webkit-text-fill-color: transparent; }
.highlight { color: var(--accent-color); }

/* --- Sections --- */
section { padding: var(--space-xl) 0; }
.section-heading {
  font-size: clamp(1.5rem, 2vw, 2rem);
  color: var(--text-color);
  margin-bottom: var(--space-lg);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.section-heading::before { content: ''; height: 2px; width: 60px; background: var(--accent-color); display: block; }

/* --- Projects --- */
.projects-grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); gap: 2rem; }
.project-card { background: var(--card-bg); padding: 2rem; border-radius: 1rem; border: 1px solid rgba(94, 234, 212, 0.05); transition: transform 0.3s ease; }
.project-card:hover { transform: translateY(-5px); }
.project-title { color: var(--text-color); font-size: 1.3rem; margin-bottom: 1rem; font-weight: 600; }
.project-details { list-style: none; color: var(--subtext-color); }
.project-details li { margin-bottom: 0.5rem; }
.project-details a { color: var(--accent-color); text-decoration: none; }

/* --- Skills & Education & Certs --- */
.skills-container, .certification-badges { display: flex; flex-wrap: wrap; gap: 1rem; }
.skill-tag, .badge { background: var(--card-bg); color: var(--text-color); padding: 0.5rem 1rem; border-radius: 20px; border: 1px solid rgba(94, 234, 212, 0.2); display: flex; align-items: center; gap: 8px; text-decoration: none; }
.education-timeline { display: flex; flex-direction: column; gap: 1.5rem; }
.education-item { background: var(--card-bg); padding: 1.5rem; border-radius: 1rem; border-left: 4px solid var(--accent-color); }
.education-item h3 { color: var(--text-color); margin-bottom: 0.5rem; }
.education-item p { color: var(--subtext-color); }
.contact-info { display: flex; flex-wrap: wrap; gap: 2rem; }
.contact-card { background: var(--card-bg); padding: 2rem; border-radius: 1rem; flex: 1; min-width: 250px; text-align: center; }
.contact-card h3 { color: var(--text-color); margin-bottom: 1rem; }
.contact-card a { color: var(--accent-color); text-decoration: none; }

/* --- Footer --- */
footer { text-align: center; padding: 2rem 0; margin-top: 4rem; border-top: 1px solid rgba(255,255,255,0.1); }
.social-links { display: flex; justify-content: center; gap: 1.5rem; margin-bottom: 1rem; }
.social-links a { color: var(--subtext-color); font-size: 1.5rem; transition: color 0.3s; }
.social-links a:hover { color: var(--accent-color); }

/* --- Responsive --- */
@media (min-width: 900px) {
  .hero { flex-direction: row-reverse; justify-content: space-between; text-align: left; padding: 0 var(--space-xl); }
  .hero-content { align-items: flex-start; max-width: 60%; }
  .hero-cta, .hero-social { justify-content: flex-start; }
  .profile-pic { margin-bottom: 0; }
}
@media (max-width: 900px) {
  .desktop-nav-links { display: none; }
  .menu-toggle { display: flex; }
}

/* ===================================
   PROJECT CARDS STYLES
   =================================== */

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2.5rem;
    margin-top: 2rem;
}

.project-card {
    background: rgba(15, 23, 42, 0.6);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: var(--radius-lg);
    overflow: hidden;
    border: 1px solid rgba(94, 234, 212, 0.1);
    transition: all 0.3s ease;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
}

.project-card:hover {
    border-color: rgba(94, 234, 212, 0.3);
    box-shadow: 0 10px 30px rgba(94, 234, 212, 0.2);
}

/* Project Image Section - FIXED */
.project-image-container {
    position: relative;
    width: 100%;
    height: 240px;
    overflow: hidden;
    background: linear-gradient(135deg, rgba(94, 234, 212, 0.1), rgba(59, 130, 246, 0.1));
}

.project-image {
    width: 100%;
    height: 100%;
    object-fit: contain; /* Changed from cover to contain */
    object-position: center; /* Center the image */
    transition: transform 0.4s ease;
}

.project-card:hover .project-image {
    transform: scale(1.05); /* Reduced scale to prevent cropping */
}

/* Project Content */
.project-content {
    padding: 1.75rem;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.project-title {
    font-size: 1.5rem;
    margin-bottom: 0.75rem;
    color: var(--accent-color);
    font-weight: 600;
}

.project-description {
    font-size: 0.95rem;
    line-height: 1.6;
    color: var(--subtext-color);
    margin-bottom: 1rem;
}

.project-details {
    list-style: none;
    padding: 0;
    margin: 1rem 0;
    flex-grow: 1;
}

.project-details li {
    font-size: 0.9rem;
    line-height: 1.7;
    color: var(--text-color);
    margin-bottom: 0.5rem;
    padding-left: 0.5rem;
}

/* Tech Tags */
.project-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.6rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
}

.project-tag {
    padding: 0.4rem 0.9rem;
    background: rgba(94, 234, 212, 0.1);
    border: 1px solid rgba(94, 234, 212, 0.25);
    border-radius: var(--radius-sm);
    font-size: 0.8rem;
    color: var(--accent-color);
    font-weight: 500;
    transition: all 0.3s ease;
}

.project-tag:hover {
    background: rgba(94, 234, 212, 0.2);
    border-color: rgba(94, 234, 212, 0.4);
    transform: translateY(-2px);
}

/* Project Links at Bottom */
.project-links-bottom {
    display: flex;
    gap: 1rem;
    margin-top: auto;
    padding-top: 1rem;
    border-top: 1px solid rgba(94, 234, 212, 0.1);
}

.project-link-btn {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.75rem 1.25rem;
    background: var(--accent-color);
    color: var(--primary-bg);
    text-decoration: none;
    border-radius: var(--radius-md);
    font-weight: 600;
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.project-link-btn:hover {
    background: #fff;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(94, 234, 212, 0.4);
}

.project-link-btn.demo {
    background: transparent;
    border: 2px solid var(--accent-color);
    color: var(--accent-color);
}

.project-link-btn.demo:hover {
    background: var(--accent-color);
    color: var(--primary-bg);
}

/* Responsive Design */
@media (max-width: 900px) {
    .projects-grid {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 2rem;
    }

    .project-image-container {
        height: 220px;
    }
}

@media (max-width: 600px) {
    .projects-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .project-image-container {
        height: 200px;
    }

    .project-content {
        padding: 1.25rem;
    }

    .project-title {
        font-size: 1.3rem;
    }

    .project-links-bottom {
        flex-direction: column;
        gap: 0.75rem;
    }

    .project-link-btn {
        width: 100%;
    }
}

/* =========================================
   MOBILE RESPONSIVENESS FIXES (Add to End)
   ========================================= */

@media screen and (max-width: 768px) {
  /* 1. Fix the Grid Columns 
     Changing minmax(350px) to 1fr ensures the card shrinks to fit the phone width 
  */
  .projects-grid {
    grid-template-columns: 1fr; /* Force single column */
    gap: 1.5rem;
    margin-top: 1.5rem;
    width: 100%;
  }

  /* 2. Reduce Container Padding 
     Gives more horizontal breathing room for the cards on small screens 
  */
  .container {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 4rem; /* Extra space at bottom for scrolling */
  }

  /* 3. Ensure Cards don't overflow */
  .project-card {
    width: 100%;
    min-width: 0; /* Critical for CSS Grid to allow shrinking */
    margin: 0 auto;
  }

  /* 4. Adjust Hero/Header spacing so it doesn't look squashed */
  .hero {
    min-height: auto; /* Allow height to be determined by content */
    padding-top: 6rem;
    padding-bottom: 3rem;
  }
  
  /* 5. Fix Image Height for mobile */
  .project-image-container {
    height: 180px;
  }
  
  /* 6. Fix Button sizes for mobile touches */
  .project-links-bottom {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .project-link-btn {
    width: 100%;
    padding: 0.85rem; /* Slightly larger touch target */
  }
}

/* Extra small devices (Galaxy Fold, iPhone SE, etc) */
@media screen and (max-width: 380px) {
  .project-title {
    font-size: 1.2rem;
  }
  
  .project-content {
    padding: 1rem;
  }
}


/* =========================================
   MOBILE RESPONSIVENESS & POLISH
   ========================================= */

/* Fix for Mobile Devices (max-width: 768px) */
@media screen and (max-width: 768px) {
    
    /* 1. Global Layout Fixes */
    body {
        overflow-x: hidden; /* Prevents side-scrolling */
    }
    
    .container {
        padding-left: 1.25rem;
        padding-right: 1.25rem;
        width: 100%;
        overflow: hidden; /* Keeps content inside */
    }

    /* 2. Projects Visibility & Layout */
    .projects-grid {
        grid-template-columns: 1fr; /* Force single column */
        gap: 2rem;
        margin-top: 1rem;
        width: 100%;
        display: flex; /* Flex is safer for stacking on some mobile browsers */
        flex-direction: column;
    }

    .project-card {
        width: 100%;
        margin: 0;
        /* Ensure opacity is visible if JS fails */
        opacity: 1; 
    }

    .project-image-container {
        height: 180px; /* Smaller height for mobile */
    }

    .project-content {
        padding: 1.25rem;
    }

    .project-title {
        font-size: 1.25rem; /* Prevent headline breaking awkwardly */
        word-wrap: break-word;
    }

    /* 3. Text Wrapping Fixes (Education/Skills) */
    .education-item h3, 
    .project-card h3,
    .section-heading {
        word-break: break-word; /* Prevents long words/names from overflowing */
        -webkit-hyphens: auto;
        hyphens: auto;
    }

    .skill-tag, .badge {
        font-size: 0.8rem; /* Smaller tags to fit screen */
        padding: 6px 12px;
    }

    /* 4. Hero Section Polish */
    .hero {
        padding-top: 5rem;
        padding-bottom: 2rem;
        min-height: auto;
    }

    .big-heading {
        font-size: 2.5rem; /* Prevent huge text breaking layout */
    }

    .profile-pic {
        width: 180px;
        height: 180px;
        margin-bottom: 1.5rem;
    }

    /* 5. Footer & Spacing */
    section {
        padding: 3rem 0; /* Reduce vertical gaps */
    }
    
    .project-links-bottom {
        flex-direction: row; /* Keep buttons side-by-side if possible */
        gap: 0.5rem;
    }
    
    .project-link-btn {
        padding: 0.6rem;
        font-size: 0.85rem;
    }
}

/* Extra small screens (iPhone SE, Fold) */
@media screen and (max-width: 380px) {
    .project-links-bottom {
        flex-direction: column; /* Stack buttons on tiny screens */
    }
    .big-heading {
        font-size: 2rem;
    }
}